import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import eve from '@/components/eve'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: 'home', component: home },
    { path: '/eve', name: 'eve', component: eve }
  ]
})



